.main-container {    
  background-image: linear-gradient(to bottom right, #00002d, #141E61);
  padding: 2%;
  height: 100%;
  contain: content;
  color: #fff;
  .navigation-bar {
    overflow: hidden;
    display: flex;
    justify-content: flex-end;
    a {
      float: left;
      display: block;
      color: #f2f2f2;
      text-align: center;
      padding: 14px 16px;
      text-decoration: none;
      font-size: 1rem;
    }
  
    /* Add an active class to highlight the current page */
    a.active {
      color: aquamarine;
    }
  
    /* Hide the link that should open and close the navigation-bar on small screens */
    .icon {
      display: none;
    }
  }

  .banner {
    margin-left: 10%;
    height: 50vh;
    .primary-text {
      font-size: 10vmin;
      font-weight: 300;
    }
    .secondary-text {
      font-size: 6vmin;
      font-weight: 300;
      color: #e5e5e5;
      display: flex;
    }
  }

  .secondary-container {
    margin-left: 10%;
    .section-container {
      float: left;
      width: 100%;
      .section-header {
        font-size: 5vmin;
      }
    }
    .exp-container {
      .single-exp {
        padding: 1rem 0;
      }
      .title {
        font-size: 3vmin;
      }
    }
  }
 


  @media screen and (max-width: 600px) {
    .navigation-bar a:not(:first-child) {
      display: none;
    }
    .navigation-bar a.icon {
      float: right;
      display: block;
    }
    .navigation-bar.responsive {
      position: relative;
    }
    .navigation-bar.responsive a.icon {
      position: absolute;
      right: 0;
      top: 0;
    }
    .navigation-bar.responsive a {
      float: none;
      display: block;
      text-align: left;
    }
  }
}
